//Блок input

import TemplateGen from '../utilities/TemplateGen'

const inputBlockTemplate = `
	<div class="form-block">
		<label class="form-label" for="{{id}}">{{label}}</label>
		<div class="input-wrapper {{errorHightlightClass}}">
			<input class="form-control input-styles {{focusHightlightClass}}" id="{{id}}" type="{{type}}" value="{{value}}" name="{{name}}">
			<div class="error-text-block">{{errorText}}</div>
		</div>
	</div>`;

export type InputParams = {
	element: string,
	id: string,
	label: string,
	value: string,
	type: string,
	name: string,
	errorText: string,
	errorHightlightClass: string,
}

export default class Input /*extends Block*/ {
	params: InputParams

	constructor(params: InputParams) {
		this.params = params;
		//super('button', props, props.className)
	}

	render(): string {
		return new TemplateGen(inputBlockTemplate).generateTemplate(this.params);
	}

	insertBlock(element: string): void {
		const inner = new DOMParser().parseFromString(new TemplateGen(inputBlockTemplate).generateTemplate(this.params), "text/html").getElementsByTagName("div")[0]; //this.element;
		const wrapper = document.querySelector(element);
		if (!inner || !wrapper) return;
		wrapper.appendChild(inner);
	}
}